import React from 'react';
import './Hotel.css'

const HotelChild = (props) => {
    // image:'https://www.oficinadeinverno.com.br/blog/wp-content/uploads/2018/11/oficina-de-inverno-o-que-voce-precisa-saber-antes-de-viajar-para-nova-york-hotel-hospedagem.jpg',
    // name:'SUNDARBAN HOTEL NO 1',
    // guest:'3',
    // berdroom:'2',
    // bath:'2',
    // comment:'Lorem ipsum data ',
    // rating:'3.2',
    // price:'100tk'
    const { image, name, guest, bedroom, bath, comment, rating, price } = props.item;
    return (
        <div>
            <div className="hotel">
                <div className="hotelPic">
                    <img src={image} />
                </div>
                <div className="hotelDetail">
                    <h4>hotel {name}</h4>
                    <p>guest:{guest} bed:{bedroom} bath:{bath}</p>
                    <p>{comment}</p>
                    <p>{price}</p>
                    <span>rating:{rating}</span>
                </div>
            </div>
        </div>
    );
};

export default HotelChild;